.js-markAllDone {
  background: none;
  border: none;
  color: teal;
  font-size: 16px;
  cursor: pointer;
  padding: 0;
  text-decoration: none;
  transition: 1s;
}

.js-markAllDone:hover {
  color: hsl(180, 100%, 35%);
}

.js-toggleShowCompleted {
  background: none;
  border: none;
  color: teal;
  font-size: 16px;
  cursor: pointer;
  padding: 0;
  text-decoration: none;
  transition: 1s;
}

.js-toggleShowCompleted:hover {
  color: hsl(180, 100%, 35%);
}